import React, { Component, PropTypes } from 'react';
import KlassName from 'teapan/klassName';

class Banner extends Component {
	constructor(props) {
		super(props);
	}

	render() {
		const props = this.props;
		const {
			inversed,
			title,
			subTitle,
			className,
			children,
			...others
		} = props;

		const klassName = KlassName(
			'banner',
			{
				['inversed']:('inversed' in props && inversed !== false),
			},
			className
		);

		return (
			<div {...others} className={klassName}>
				<h2 className="banner-title">{title}</h2>
				<h3 className="banner-subtitle">{subTitle}</h3>
				{ children ? <div className="banner-content">{children}</div> : null }
			</div>
		);
	}
};

Banner.propTypes = {
	inversed: PropTypes.bool,
	title: PropTypes.string,
	subTitle: PropTypes.string,
	className: PropTypes.string,
	children: PropTypes.node,
};

Banner.defaultProps = {
};

export default Banner;